<template>
  <a :href="displayLink" target="_blank" class="btn">
    {{ displayText }}
  </a>
</template>

<script>
export default {
  props: {
    link: String,
    text: String,
  },

  data() {
    return {
      displayLink: this.link,
      displayText: this.text,
    };
  },
};
</script>



